module.exports = "\n<div>\n\t\t<section class=\"markdown\">\n      <h1>Pagination 分页</h1>\n      <p>\n        采用分页的形式分隔长列表，每次只加载一个页面。\n      </p>\n      <h2>何时使用</h2>\n      <ul>\n        <li>当加载/渲染所有数据将花费很多时间时；</li>\n        <li>可切换页码浏览数据。</li>\n      </ul>\n      <h2>组件演示</h2>\n    </section>\n  <v-Row :gutter=\"16\">\n    <v-Col span=\"12\">\n    \t<code-box\n        title=\"基本\"\n        describe=\"基础分页。\"\n      > \n        <v-pagination \n\t\t\t  \t:total=\"50\">\n\t\t\t\t</v-pagination>\n      </code-box>\n\n      <code-box\n        title=\"改变\"\n        describe=\"改变每页显示条目数。\"\n      > \n        <v-pagination \n\t\t\t  \t:show-size-changer=\"true\"\n\t\t\t  \t:on-show-size-change=\"pageSizeChange\"\n\t\t\t  \t:total=\"50\">\n\t\t\t\t</v-pagination>\n\t\t\t\t<template slot=\"js\">\n\t\t\t\texport default {\n\t\t\t\t\tmethods: {\n\t\t\t\t\t\tpageSizeChange(current, size){\n\t\t\t\t\t\t\tconsole.log(current, size);\n\t\t\t\t\t\t}\n\t\t\t\t\t}\n\t\t\t\t}\n\t\t\t</template>\n      </code-box>\n\n      <code-box\n        title=\"小尺寸\"\n        describe=\"小尺寸分页。\"\n       > \n        <v-pagination \n\t        size=\"small\"\n\t\t\t  \t:total=\"50\">\n\t\t\t\t</v-pagination>\n\t\t\t  <br>\n        <v-pagination \n\t\t\t  \t:current.sync=\"current\"\n\t\t\t  \t:total=\"100\"\n\t\t\t  \t:default-page-size=\"5\"\n\t\t\t  \t:page-size=\"10\"\n\t\t\t  \t:on-change=\"loadPage\"\n\t\t\t  \t:show-size-changer=\"true\"\n\t\t\t  \t:on-show-size-change=\"pageSizeChange\"\n\t\t\t  \t:show-quick-jumper=\"true\"\n\t\t\t  \tsize=\"small\"\n\t\t\t  \t:simple=\"false\"></v-pagination>\n\t\t\t  <br>\n\t\t  \t<v-pagination \n\t\t\t  \t:current.sync=\"current\"\n\t\t\t  \t:total=\"50\"\n\t\t\t  \t:default-page-size=\"5\"\n\t\t\t  \t:page-size=\"10\"\n\t\t\t  \t:on-change=\"loadPage\"\n\t\t\t  \tsize=\"small\"\n\t\t\t  \t:simple=\"false\"\n\t\t\t  \t:show-total=\"showTotal\"></v-pagination>\n\t\t\t  <template slot=\"js\">\n\t\t\t\t  export default {\n\t\t\t\t\t\tdata(){\n\t\t\t\t\t\t\tr